import { Button } from "@mui/material";
import React, { Fragment, useState } from "react";
import { Link, useParams } from "react-router-dom";
import "./customerdetails.scss";
import Summary from "./Summary";
import Notes from "./Notes";
import Documents from "./Documents";
import BookingDialog from "../../booking/BookingDialog";

export default function CustomerDetails() {
  const { customId } = useParams();
  const date = new Date().toISOString().slice(0, 10);

  const [activeIndex, setActiveIndex] = useState("1");

  const handleIndexClick = (item) => {
    setActiveIndex(item);
  };

  return (
    <Fragment>
      <div className="named-heading">
        <p>Thejas</p>
        <div className="book-edit">
          <BookingDialog />
          {/* <Link to={"/book-car"} className="button">
            <Button variant="contained" style={{}}>
              Book a car
            </Button>
          </Link> */}
          <Link to={"/edit-customer"} className="button">
            <Button variant="outlined" style={{}}>
              Edit
            </Button>
          </Link>
        </div>
      </div>

      <div className="customer-details">
        <div className="photo">
          <img
            className="customer-photo"
            src="data:image/jpeg;base64,"
            alt=""
          />
        </div>
        <div className="customer-info">
          <div className="info">
            <h4>Phone numer</h4>
            <p>9999999999</p>
          </div>
          <div className="info">
            <h4>Email</h4>
            <p>thejas@email.com</p>
          </div>
          <div className="info">
            <h4>Address</h4>
            <p>Kasaragod, Kerala, India</p>
          </div>
          <div className="info">
            <h4>License</h4>
            <p>{customId}</p>
          </div>
          <div className="info">
            <h4>License Expiry</h4>
            <p>{date}</p>
          </div>
        </div>
      </div>

      <div className="tab-details">
        <div className="tab-heads">
          <div
            className={`tab ${activeIndex === "1" ? "active" : ""}`}
            onClick={() => handleIndexClick("1")}
          >
            Summary
          </div>

          <div
            className={`tab ${activeIndex === "2" ? "active" : ""}`}
            onClick={() => handleIndexClick("2")}
          >
            Notes
          </div>

          <div
            className={`tab ${activeIndex === "3" ? "active" : ""}`}
            onClick={() => handleIndexClick("3")}
          >
            Documents
          </div>
        </div>
        <div className="tab-display">
          {activeIndex == 1 && <Summary />}
          {activeIndex == 2 && <Notes />}
          {activeIndex == 3 && <Documents />}
        </div>
      </div>
    </Fragment>
  );
}
